<template>
  <div class="mx-4 mx-sm-9 reservation-info" :class="{'mb-0': checkinSwitch}">
    <h3 class="mb-7 font-size-xl reservation-subtitle" :class="{'mt-1': checkinSwitch}">
      :
      <br>
      {{ room.name }}
    </h3>
    <p>
      {{ room.description }}
    </p>
    <ul class="pa-0">
      <li class="mb-4">
        : {{
          room.descriptionShort.GuestMax === room.descriptionShort.GuestMin
              ? room.descriptionShort.GuestMin
              : `${room.descriptionShort.GuestMin}~${room.descriptionShort.GuestMax}`
        }}
      </li>
      <li class="mb-4">
        : {{ room.descriptionShort.Footage }}
      </li>
      <li class="mb-11">
        {{ roomContent }}
      </li>
    </ul>
    <div class="secondary primary-color font-weight-bold text-center py-3 px-15 mb-8">
      {{ roomAmenities }}
    </div>
    <div class="mx-6 mx-md-12 d-flex justify-space-between" v-if="!checkinSwitch">
      <ul class="pa-0">
        <li>
          <div class="font-size-xs">checkIn </div>
          <div class="font-size-lg">{{ room.checkInAndOut.checkInEarly }}~{{ room.checkInAndOut.checkInLate }}</div>
        </li>
        <li class="mt-3">
          <div class="font-size-xs">(~)</div>
          <div class="font-size-lg">${{ room.normalDayPrice }}</div>
        </li>
      </ul>
      <ul class="pa-0">
        <li>
          <div class="font-size-xs">checkout </div>
          <div class="font-size-lg">{{ room.checkInAndOut.checkOut }}</div>
        </li>
        <li class="mt-3">
          <div class="font-size-xs">(~)</div>
          <div class="font-size-lg">${{ room.holidayPrice }}</div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "ReservationInfo",
  props: {
    room: {
      type: Object,
      default: () => {},
    },
    checkinSwitch: {
      type: Boolean,
      default: false,
    }
  },
  computed: {
    roomContent() {
      let beds = {};
      this.room.descriptionShort.Bed.forEach(function(item) {
        if(beds[item]) {
          beds[item] += 1;
        } else {
          beds[item] = 1;
        }
      });

      let str = "";
      Object.keys(beds).forEach(function(item) {
        str += `${beds[item]}${item}`;
      });

      if (this.room.descriptionShort["Private-Bath"] > 0) {
        str += `, ${this.room.descriptionShort["Private-Bath"]}`;
      }
      return str;
    },
    roomAmenities() {
      let amenities = [];
      if (this.room.amenities) {
        const translate = {
          "Wi-Fi": "wifi",
          Breakfast: "",
          "Mini-Bar": "",
          "Room-Service": "",
          Television: "",
          "Air-Conditioner": "",
          Refrigerator: "",
          Sofa: "",
          "Great-View": "",
          "Smoke-Free": "",
          "Child-Friendly": "",
          "Pet-Friendly": "",
        };

        Object.keys(this.room.amenities).forEach((el) => {
          if (this.room.amenities[el]) {
            amenities.push(` ${translate[el]} `);
          }
        });
      }
      return amenities.toString();
    },
  },
}
</script>

